import { lazy } from 'react';
import  Base  from './Layout/Base';

// machines
const DashboardMachine = lazy(() => import('./Dashboard/Dashboard_machine'));
const MapMachine = lazy(()=> import('./Maps/MapMachine'));
const ListMachine = lazy(()=> import ('./CRUD/ListMachine'));
const AddMachine = lazy(()=> import('./CRUD/Add'));
const ShowMachine = lazy(()=> import('./CRUD/ShowMachine'));
const EditMachine = lazy(() => import('./CRUD/Edit'));
const ListActivite = lazy(() => import('./Activites/ListActivite'));
const ListAlerte = lazy(()=> import ('./Notifications/ListAlerte'));
const Carte = lazy(()=> import ('./Cartographie/Carte'));
const Indisponible = lazy(() => import ('./Indisponible'));

export  {
    DashboardMachine,
    MapMachine,
    AddMachine,
    ShowMachine,
    EditMachine,
    ListMachine,
    ListActivite,
    ListAlerte,
    Carte,
    Indisponible as IndisponibleMachine,
    Base as BaseMachine,
}
