import { lazy } from 'react';
import Base from './Layout/Base';

// admin
const DashboardAdmin = lazy(() => import('./Dashboard/Dashboard_admin'));
const AddManager = lazy(() => import('./Manager/AddManager'));
const AddRole = lazy(() => import('./Role/AddRole'));
const Planning = lazy(() => import('./Planning/Calendar'));
const Indisponible = lazy(() => import ('./Indisponible'));
const Compte =  lazy(() => import ('./Compte/Profil'));

export  {
    DashboardAdmin,
    AddManager,
    AddRole,
    Planning,
    Indisponible as IndisponibleAdmin,
    Compte as CompteAdmin,
    Base as BaseAdmin,
}