import React, { Suspense, lazy, useState} from 'react';
import { withRouter, Switch, Route, Redirect } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import firebase from './firebase';

/* loader component for Suspense*/
import PageLoader from './components/Common/PageLoader';

import BasePage from './BasePage';
// import BaseHorizontal from './components/Layout/BaseHorizontal';

/** admin components */
import {
    DashboardAdmin,
    AddManager,
    AddRole,
    Planning,
    IndisponibleAdmin,
    CompteAdmin,
    BaseAdmin,
}
from './components/Administrateur/Routes';

/** machines components */
import { 
    DashboardMachine, 
    MapMachine, 
    AddMachine,
    ShowMachine,
    EditMachine,
    ListMachine,
    ListActivite,
    ListAlerte,
    Carte,
    IndisponibleMachine,
    BaseMachine,
} from './components/Machine/Routes';

/**user components */
import {
    DashboardClient,
    ListeClient,
    InfoClient,
    ListePartenaire,
    InfoPartenaire,
    AddPartenaire,
    Abonnement,
    ListeService,
    AddService,
    ShowService,
    ListeActualite,
    Compte,
    BaseClient,
} from './components/Client/Routes';


import axios from 'axios';
import { url_managers } from './urls';

/*authentication page*/
//const Login = lazy(() => import('./components/Pages/Login'));
const Login = lazy(() => import('./components/Authentication/SignInUser'));
const Register = lazy(() => import('./components/Authentication/Manager/CompleteSignUp')); // le manager s'inscrit lui même avant d'avoir accès au système
const Recover = lazy(() => import('./components/Pages/Recover'));
const Lock = lazy(() => import('./components/Pages/Lock'));
const NotFound = lazy(() => import('./components/Pages/NotFound'));
const Error500 = lazy(() => import('./components/Pages/Error500'));
const Maintenance = lazy(() => import('./components/Pages/Maintenance'));

// JUST FOR TEST
const MapTest = lazy(() => import ('./components/MesTests/Maps/MapTest'));

/* Used to render a lazy component with react-router */
const waitFor = Tag => props => <Tag {...props}/>;

const Routes = ({ location }) => {
    const currentKey = location.pathname.split('/')[1] || '/';
    const timeout = { enter: 500, exit: 500 };
    const animationName = 'rag-fadeInRight' // animations supported 'rag-fadeIn', 'rag-fadeInRight', 'rag-fadeInLeft'
    
    const [user, setUser] = useState("");
    const [role, setRole] = useState("");
    const [isRegister, setRegister] = useState("");

    const isUser = ( async (uid) => {
        let manager = {}
        await axios.get(`${url_managers}${uid}`)
        .then((res) => {
            //console.log(res)
            manager = res.data.data
            setRegister(manager.complete)
            setRole(manager.role.nom)
        })
        .catch((error) => {
            console.log(error)
        })
    })

    const PageAuth = () => {
        return (
            <BasePage>
                <Suspense fallback={<PageLoader/>}>
                    <Switch>
                        <Route exact path="/login" component={waitFor(Login)}/>
                        <Route exact path="/register/" component={waitFor(Register)}/>
                        <Route exact path="/recover" component={waitFor(Recover)}/>
                        <Route exact path="/lock" component={waitFor(Lock)}/>
                        <Route exact path="/notfound" component={waitFor(NotFound)}/>
                        <Route exact path="/error500" component={waitFor(Error500)}/>
                        <Route exact path="/maintenance" component={waitFor(Maintenance)}/>
                        <Route exact path="/test" component={waitFor(MapTest)}/>
                    </Switch>
                </Suspense>
            </BasePage>
        )
    }

    const PageAdmin = () => {
        return (
            <BaseAdmin>
                <TransitionGroup>
                <CSSTransition key={currentKey} timeout={timeout} classNames={animationName} exit={false}>
                    <div>
                        <Suspense fallback={<PageLoader/>}>
                            <Switch>
                                <Route path="/dashboard_admin" component={waitFor(DashboardAdmin)}/>
                                <Route path="/add_role" component={waitFor(AddRole)}/>    
                                <Route path="/add_manager" component={waitFor(AddManager)}/>
                                <Route path="/planning" component={waitFor(Planning)}/>
                                <Route path="/indisponible" component={waitFor(IndisponibleAdmin)}/>
                                <Route path="/compte" component={waitFor(CompteAdmin)}/>
                                <Redirect to="/dashboard_admin"/>
                            </Switch>
                        </Suspense>
                    </div>
                </CSSTransition>
                </TransitionGroup>
            </BaseAdmin>
        )
    }

    const PageGreenBox = () => {
        return (
            <BaseMachine>
                <TransitionGroup>
                <CSSTransition key={currentKey} timeout={timeout} classNames={animationName} exit={false}>
                    <div>
                        <Suspense fallback={<PageLoader/>}>
                            <Switch>
                                <Route path="/dashboard_machine" component={waitFor(DashboardMachine)}/>
                                <Route path="/map_machine" component={waitFor(MapMachine)}/>
                                <Route path="/carte_machine" component={waitFor(Carte)}/>
                                <Route path="/list_machine" component={waitFor(ListMachine)}/>
                                <Route path="/add_machine" component={waitFor(AddMachine)}/>
                                <Route path="/edit_machine/:id" component={waitFor(EditMachine)}/>
                                <Route path="/show_machine/:id" component={waitFor(ShowMachine)}/>
                                <Route path="/list_activite/" component={waitFor(ListActivite)}/>                        
                                <Route path="/list_alerte" component={waitFor(ListAlerte)}/>
                                <Route path="/indisponible" component={waitFor(IndisponibleMachine)}/>
                                <Route exact path="/test" component={waitFor(MapTest)}/>
                                <Redirect to="/dashboard_machine"/>
                            </Switch>
                        </Suspense>
                    </div>
                </CSSTransition>
                </TransitionGroup>
            </BaseMachine>
        )
    }

    const PageGreenUser = () => {
        return (
            <BaseClient>
                <TransitionGroup>
                <CSSTransition key={currentKey} timeout={timeout} classNames={animationName} exit={false}>
                    <div>
                        <Suspense fallback={<PageLoader/>}>
                            <Switch>
                                <Route path="/dashboard_client" component={waitFor(DashboardClient)}/>
                                <Route path="/list_client" component={waitFor(ListeClient)}/>
                                <Route path="/info_client" component={waitFor(InfoClient)}/>
                                <Route path="/list_partenaire" component={waitFor(ListePartenaire)}/>
                                <Route path="/show_partenaire/:id" component={waitFor(InfoPartenaire)}/>
                                <Route path="/add_partenaire" component={waitFor(AddPartenaire)}/>
                                <Route path="/abonnements" component={waitFor(Abonnement)}/>
                                <Route path="/list_service" component={waitFor(ListeService)}/>
                                <Route path="/add_service" component={waitFor(AddService)}/>                        
                                <Route path="/show_service/:id" component={waitFor(ShowService)}/>                        
                                <Route path="/list_alerte" component={waitFor(ListAlerte)}/>
                                <Route path="/actualites" component={waitFor(ListeActualite)}/>
                                <Route path="/compte" component={waitFor(Compte)}/>
                                <Redirect to="/dashboard_client"/>
                            </Switch>
                        </Suspense>
                    </div>
                </CSSTransition>
                </TransitionGroup>
            </BaseClient>
        )
    }

    const PagePartenaire = () => {
        return (
            <div> Un attimo partenaire... </div>
        )
    }

    const PageNoProfil = () => {
        return (
            <div>
                 <p> Aucun profil n'est défini pour cet utilisateur </p>
                 <span> Veuillez demandez à l'administrateur de vous attribuer un rôle </span>
                 <hr/> <span> Merci </span>
                 <span> Adresse : <em> <b> greenplan.user237@gmail.com </b> </em> </span>
            </div>
        )
    }

    //En travail sur la vue client
    //utilisateur n'est pas authentifié
    firebase.auth().onAuthStateChanged( (authUser) => {
        if(authUser) {
            setUser(authUser)
            const uid = authUser.uid
            isUser(uid)
            //console.log(role, isRegister)
        }
        else { // l'utilisateur n'est pas authentifié          
            setUser("")
        }
    })

    if(!user) {
        return (
            <>
                <PageAuth/>
                <Redirect to="/login"/>
            </>
        )
    } else {
        console.log(user)
        /*
        if(!isRegister) {
            return (
                <>
                    <PageAuth/>
                    <Redirect to='/register'/>
                </>
            ) 
        }*/
        if(!role) {
            return(<PageLoader/>)
        }
        return(
            (role == 'admin')? (
                <PageAdmin/>
            ) : (role== 'greenbox manager')? (
                <PageGreenBox/>
            ) : (role== 'greenuser manager') ? (
                <PageGreenUser/>
            ) : (role== 'partenaire') ? (
                <PagePartenaire/>
            ) : (
                <PageNoProfil/>
            )
            /* work on green user dashboard
            return (
                <PageGreenUser/>
            )*/
        )  
    }
}

export default withRouter(Routes);
