//réinitialisation du mot de passe
const sendPasswordResetEmail = async (email) => {
  try {
    await auth.sendPasswordResetEmail(email);
    alert("Password reset link sent!");
  } catch (err) {
    console.error(err);
    alert(err.message);
  }
};

// deconnexion
const logout = () => {
  auth.signOut();
};



