import { lazy } from 'react';
import Base from './Layout/Base';

//dashboard
const DashboardClient = lazy(() => import('./Dashboard/Dashboard_Client'));
// greens
const ListeClient = lazy(() => import('./GreenUser/Liste'));
const InfoClient = lazy(() => import('./GreenUser/Info_Client'));
const AddPartenaire = lazy(() => import('./Partenaires/Add_Partenaire'));
// partenaires
const ListePartenaire = lazy(() => import('./Partenaires/Liste_Partenaire'));
const InfoPartenaire = lazy(() => import('./Partenaires/Info_Partenaire'));
//services
const Abonnement = lazy(() => import('./Services/Abonnement'));
const ListeService = lazy(() => import('./Services/Liste_Services'));
const AddService = lazy(() => import('./Services/Add_Service'));
const ShowService = lazy(() => import('./Services/Show_Service'));

//actualites
const ListeActualite = lazy(() => import('./Actualites/Liste_Actualites'));

//profil
const Compte = lazy(() => import('../Compte/Profil'));

export  {
    DashboardClient,
    ListeClient,
    InfoClient,
    ListePartenaire,
    InfoPartenaire,
    AddPartenaire,
    Abonnement,
    ListeService,
    AddService,
    ShowService,
    ListeActualite,
    Compte,
    Base as BaseClient,
}