import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, ListGroup, ListGroupItem } from 'reactstrap';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from '../../../store/actions/actions';

import ToggleFullscreen from '../../Common/ToggleFullscreen';
import HeaderRun from './Header.run';
import swal from "sweetalert";


class Header extends Component {

    componentDidMount() {
        HeaderRun();
    }

    toggleUserblock = e => {
        e.preventDefault();
        this.props.actions.toggleSetting('showUserBlock');
    }

    toggleCollapsed = e => {
        e.preventDefault()
        this.props.actions.toggleSetting('isCollapsed');
        this.resize()
    }

    toggleAside = e => {
        e.preventDefault()
        this.props.actions.toggleSetting('asideToggled');
    }

    resize () {
        // all IE friendly dispatchEvent
        var evt = document.createEvent('UIEvents');
        evt.initUIEvent('resize', true, false, window, 0);
        window.dispatchEvent(evt);
        // modern dispatchEvent way
        // window.dispatchEvent(new Event('resize'));
    }

    goBack() {
        {swal("Désolée... Cette fonctionnalité n'est pas encore disponible", "Notre équipe y travaille et elle sera disponsible à partir du 06/09/21. Merci de votre compréhension", "warning")}  
    }

    render() {
        return (
            <header className="topnavbar-wrapper">
                { /* START Top Navbar */ }
                <nav className="navbar topnavbar">
                    { /* START navbar header */ }
                    <Link to="/" className="navbar-header">
                        <div className=" navbar-brand brand-logo-collapsed">
                            <em className="fas fa-recycle text-success fa-1x"></em>
                            <small className="text-yellow"> AM</small>
                        </div>                        
                        <div className=" navbar-brand brand-logo">
                            <em className="fas fa-recycle text-success fa-1x"></em>
                            <small className="text-yellow"> Green AppManager</small>
                        </div>                    
                    </Link>
                    { /* END navbar header */ }
                    { /* START Left navbar */ }
                    <ul className="navbar-nav mr-auto flex-row">
                        <li className="nav-item">
                            { /* Button used to collapse the left sidebar. Only visible on tablet and desktops */ }
                            <a href="" className="nav-link d-none d-md-block d-lg-block d-xl-block" onClick={ this.toggleCollapsed }>
                                <em className="fas fa-bars"></em>
                            </a>
                            { /* Button to show/hide the sidebar on mobile. Visible on mobile only. */ }
                            <a href=""  className="nav-link sidebar-toggle d-md-none" onClick={ this.toggleAside }>
                                <em className="fas fa-bars"></em>
                            </a>
                        </li>
                    </ul>
                    { /* END Left navbar */ }
                    { /* START Right Navbar */ }
                    <ul className="navbar-nav flex-row flex-space-around flex-space-between">
                        {/*Home icon */}
                        <li className="nav-item d-none d-md-block pr-4">
                            <Link to="/dashboard_machine" className="nav-link">
                                <em className="icon-home fa-1x"></em>
                                <span> Home </span>
                            </Link>
                        </li>
                        {/*Clients icon */}
                        <li className="nav-item d-none d-md-block pr-4 pl-3">
                            <Link to="/list_client"  className="nav-link">
                                <em className="fas fa-users fa-1x"></em>
                                <span> Clients </span>
                            </Link>
                        </li>
                        {/*Services icon */}
                        <li className="nav-item d-none d-md-block pr-4 pl-3">
                            <Link to="/list_service" className="nav-link">
                                <em className="icon-present fa-1x"></em>
                                <span> Services </span>
                            </Link>
                        </li>
                        {/*Partenaires icon */}
                        <li className="nav-item d-none d-md-block pr-4 pl-3">
                            <Link to="/list_partenaire" className="nav-link">
                                <em className="fas fa-user-tie fa-1x"></em>
                                <span> Partenaires </span>
                           </Link>
                        </li>
                        { /* START Alert menu */ }
                        <UncontrolledDropdown nav inNavbar className="dropdown-list pr-4 pl-3">
                            <DropdownToggle nav className="dropdown-toggle-nocaret">
                                <em className="icon-bell fa-1x"></em>
                                <span className="badge badge-danger">11</span>
                                <span> Alertes </span>
                            </DropdownToggle>
                            { /* START Dropdown menu */ }
                            <DropdownMenu right className="dropdown-menu-right animated pulse">
                                <DropdownItem>
                                    { /* START list group */ }
                                    <ListGroup>
                                       <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()}>
                                          <div className="media">
                                             <div className="align-self-start mr-2">
                                                <em className="fas fa-plus-circle fa-2x text-info"></em>
                                             </div>
                                             <div className="media-body">
                                                <p className="m-0">Nouvelle GreenBox</p>
                                                <p className="m-0 text-muted text-sm">2 nouvelles GreenBox</p>
                                             </div>
                                          </div>
                                       </ListGroupItem>
                                       <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()}>
                                          <div className="media">
                                             <div className="align-self-start mr-2">
                                                <em className="fas fa-bolt fa-2x text-warning"></em>
                                             </div>
                                             <div className="media-body">
                                                <p className="m-0">GreenBox injoignales</p>
                                                <p className="m-0 text-muted text-sm">Désolé, nous n'arrivons pas à joindre 2 GreenBoxs</p>
                                             </div>
                                          </div>
                                       </ListGroupItem>
                                       <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()}>
                                          <div className="media">
                                             <div className="align-self-start mr-2">
                                                <em className="fa fa-tasks fa-2x text-success"></em>
                                             </div>
                                             <div className="media-body">
                                                <p className="m-0">GreenBoxs pleines</p>
                                                <p className="m-0 text-muted text-sm">Déjà 4 GreenBoxs pleines !</p>
                                             </div>
                                          </div>
                                       </ListGroupItem>
                                       <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()}>
                                          <span className="d-flex align-items-center">
                                             <span className="text-sm">Autres</span>
                                             <span className="badge badge-danger ml-auto">14</span>
                                          </span>
                                       </ListGroupItem>
                                    </ListGroup>
                                    { /* END list group */ }
                                </DropdownItem>
                            </DropdownMenu>
                            { /* END Dropdown menu */ }
                        </UncontrolledDropdown>
                        { /* END Alert menu */ }                        
                        {/*Chart icon */}
                        <li className="nav-item d-none d-md-block pr-3 pl-3">
                            <div onClick={this.goBack} className="nav-link">
                                <em className="icon-chart fa-1x"></em>
                                <span> Statistisques </span>
                            </div>
                        </li>
                        { /* START Settings menu */ }
                        <UncontrolledDropdown nav inNavbar className="dropdown-list pr-4 pl-3">
                            <DropdownToggle nav className="dropdown-toggle-nocaret">
                                    <em className="icon-settings fa-1x"></em>
                                    <span> Paramètres </span>
                            </DropdownToggle>
                            { /* START Dropdown menu */ }
                            <DropdownMenu right className="dropdown-menu-right animated pulse">
                                <DropdownItem>
                                    { /* START list group */ }
                                    <ListGroup>
                                       { /* Search icon */ }
                                       <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()} data-search-onSuspend="">
                                          <div className="media">
                                             <div className="align-self-start mr-2">
                                                  <em className="icon-magnifier fa-2x text-info"></em>
                                             </div>
                                             <div className="media-body">
                                                <p className="m-0">Recherche</p>
                                                <p className="m-0 text-muted text-sm">
                                                    Vous cherchez quelque chose ? Cliquer ici  <em className="far fa-smile fa-1x text-warning"> </em>
                                                </p>
                                             </div>
                                            </div>
                                        </ListGroupItem>
                                       { /* Fullscreen (only desktops) */ }
                                        <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()}>
                                           <div className="media">
                                             <div className="align-self-start mr-2">
                                                <ToggleFullscreen className="nav-link"/>
                                             </div>
                                             <div className="media-body">
                                                <p className="m-0">Mode plein écran</p>
                                                <p className="m-0 text-muted text-sm">Activer le mode plein écran</p>
                                             </div>
                                          </div>
                                        </ListGroupItem>
                                       <ListGroupItem action tag="a" href="" onClick={e => e.preventDefault()}>
                                          <div className="media">
                                             <div className="align-self-start mr-2">
                                                <em className="fas fa-arrow-alt-circle-right fa-2x text-purple"></em>
                                             </div>
                                             <div className="media-body">
                                                <p className="m-0">Autres</p>
                                                <p className="m-0 text-muted text-sm">Configurer les paramètres des GreenBoxs</p>
                                             </div>
                                          </div>
                                       </ListGroupItem>
                                    </ListGroup>
                                    { /* END list group */ }
                                </DropdownItem>
                            </DropdownMenu>
                            { /* END Dropdown menu */ }
                        </UncontrolledDropdown>
                        { /* END Settings menu */ }
                        { /* START User avatar toggle */ }
                        <li className="nav-item d-none d-md-block">
                            <a  className="nav-link" onClick={ this.goBack }>
                                <em className="icon-user fa-1x"></em>
                                <span> Mon compte </span>
                            </a>
                        </li>
                        { /* END User avatar toggle */ }                                             
                    </ul>
                    { /* END Right Navbar */ }

                    { /* START Search form */ }
                    <form className="navbar-form" role="search" action="search.html">
                       <div className="form-group">
                          <input className="form-control" type="text" placeholder="Type and hit enter ..."/>
                          <div className="fa fa-times navbar-form-close" data-search-dismiss=""></div>
                       </div>
                       <button className="d-none" type="submit">Submit</button>
                    </form>
                    { /* END Search form */ }
                </nav>
                { /* END Top Navbar */ }
            </header>
            );
    } 
}

Header.propTypes = {
    actions: PropTypes.object,
    settings: PropTypes.object
};

const mapStateToProps = state => ({ settings: state.settings })
const mapDispatchToProps = dispatch => ({ actions: bindActionCreators(actions, dispatch) })

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Header);